import React from "react"
import { Link } from "gatsby"

import Layout from "../components/Layout"
import SEO from "../components/SEO"

interface Question {
  text: string
}

interface Questioner {
  name: string
  questions: Question[]
}

const questioners: Questioner[] = [
  {
    name: "questioner 1",
    questions: [
      { text: "quetion 1" },
      { text: "quetion 2" },
      { text: "quetion 3" },
      { text: "quetion 4" },
      { text: "quetion 5" },
    ],
  },
  {
    name: "questioner 2",
    questions: [
      { text: "quetion 1" },
      { text: "quetion 2" },
      { text: "quetion 3" },
      { text: "quetion 4" },
      { text: "quetion 5" },
    ],
  },
]

enum Steps {
  SelectQuestioner,
  CreateQuestioner,
  CreateRoom,
}

type SelectQuestionerProps = { onSubmit: (q: Questioner) => void }

// todo: we need a page that can display and edit questioner and question

const SelectQuestioner = ({ onSubmit }: SelectQuestionerProps) => {
  const [questioner, setQuestioner] = React.useState<Questioner | undefined>(
    undefined
  )
  return (
    <form onSubmit={() => questioner && onSubmit(questioner)}>
      <h2>Select a questioner</h2>
      {questioners.map(q => {
        const { name, questions } = q
        return (
          <div key={name}>
            <label>
              <input
                onChange={e => setQuestioner(q)}
                type="radio"
                name="questions"
              />
              {name}
            </label>
            <ol>
              {questions.map(({ text }) => (
                <li key={text}>
                  <p>{text}</p>
                </li>
              ))}
            </ol>
          </div>
        )
      })}
      <input type="submit" disabled={!questioner} value="Create" />
    </form>
  )
}

const CreateRoom = ({
  questioner: { name, questions },
}: {
  questioner: Questioner
}) => {
  return (
    <div>
      <h2>Questioner</h2>
      <div key={name}>
        <h4>{name}</h4>
        <ol>
          {questions.map(({ text }) => (
            <li key={text}>
              <p>{text}</p>
            </li>
          ))}
        </ol>
      </div>
      <button>
        <Link to={"/room-dashboard"}> Open a room from this questioner </Link>
      </button>
    </div>
  )
}

const SecondPage = () => {
  const [questioner, setQuestioner] = React.useState<Questioner | undefined>(
    undefined
  )
  const [step, setStep] = React.useState(Steps.SelectQuestioner)

  const questionSelectedHandler = React.useCallback(q => {
    setQuestioner(q)
    setStep(Steps.CreateRoom)
  }, [])

  // setStep(Steps.CreateRoom)

  return (
    <Layout>
      <SEO title="Page two" />

      {step === Steps.SelectQuestioner && (
        <SelectQuestioner onSubmit={questionSelectedHandler} />
      )}
      {step === Steps.CreateRoom && questioner && (
        <CreateRoom questioner={questioner} />
      )}
      <Link to="/">Go back to the homepage</Link>
    </Layout>
  )
}

export default SecondPage
